@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&family=Playfair+Display:ital@0;1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
.about {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: rgb(253, 240, 223);
  height: 160vh;
}

.aboutLeft {
  width: 600px;
  /* padding: 1rem 2rem; */
}

.aboutLeft h3 {
  font-size: 34px;
  font-family: sans-serif;
  color: #3b967d;
}
.aboutLeft p {
  width: 580px;
  line-height: 1.7;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}

.aboutLeft span {
  line-height: 4;
}

.aboutLeft span a {
  color: #3b967d;
}

.aboutRight {
  width: 400px;
}

.aboutRight img {
  border-radius: 50%;
  border: 8px solid #3b967d;
  width: 400px;
  height: 400px;
  background-position: center;
  background-size: cover;
  margin-top: 2rem;
}

.aboutRight p {
  font-size: 38px;
  width: 350px;
  color: #3b967d;
}

.aboutRight span {
  background-color: #3b967d;
  padding: 10px 16px;
  border-radius: 7px;
}
.aboutRight span a {
  text-decoration: none;
  color: aliceblue;
}
.aboutRight span:hover {
  background-color: #256957;
}

.aboutRight span a i {
  margin-right: 6px;
}

.appdesign {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 400px;
  padding: 0 4rem;
}

.aLeft {
  background-color: rgb(253, 240, 223);
  border-radius: 12px;
  padding: 1rem;
  width: 480px;
}
.aLeft h3,
.aLeft span {
  color: rgb(209, 148, 72);
}

.aRight span i,
.aLeft span i {
  font-size: 16px;
  margin-left: 5px;
}
.aLeft span i:hover,
.aRight span i:hover {
  -webkit-transform: translateX(2px);
          transform: translateX(2px);
}

.aRight span:hover,
.aLeft span:hover {
  -webkit-transform: translateX(2px);
          transform: translateX(2px);
  text-decoration: underline;
}

.aRight {
  background-color: #3b967d;
  border-radius: 12px;
  padding: 1rem;
  width: 480px;
}

.aRight h3,
.aRight span {
  color: rgb(218, 252, 243);
}

.aLeft p,
.aRight p {
  font-size: 34px;
  width: 400px;
  margin-top: 1rem;
}

.contact {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #b9d6cf;
  padding: 2rem 6rem;
}

/* first */

.first,
.second,
.third {
  width: 400px;
}

.second {
  padding-left: 3.5rem;
}

.first h3 {
  font-size: 28px;
  color: #3b967d;
}

.para {
  font-weight: 600;
  font-style: italic;
}
.first p {
  line-height: 1.4;
}

.social {
  display: flex;
}
.social span {
  background-color: #246453;
  border-radius: 50%;
  margin: 6px;
}

.social span i {
  width: 20px;
  height: 20px;
  color: #fff;
  padding: 12px;
}

/* second */
.call h3,
.location h3,
.services h3 {
  color: #3b967d;
  font-size: 18px;
}

.second ul {
  padding: 0;
}

.second ul li {
  list-style: none;
  font-size: 16px;
}

/* third */

form input {
  border-radius: 12px;
  padding: 12px;
  outline: none;
  border: 1px solid #eee;
  margin: 12px 0;
  width: 300px;
}

.third textarea {
  outline: none;
  border: none;
  border-radius: 12px;
  padding: 12px;
}

.third button {
  background-color: #3b967d;
  width: 328px;
  padding: 12px 10px;
  border-radius: 12px;
  color: #fff;
  border: none;
  margin-top: 1rem;
  cursor: pointer;
}
.third button:hover {
  background-color: #246453;
}

.download {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  background-color: #b9d6cf;
}

.downloadContent {
  background-color: #fff;
  width: 700px;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  bottom: 6rem;
}
.downloadContent h3 {
  padding: 20px 0;
}
.store span {
  background-color: #3b967d;
  padding: 1rem;
  margin: 1rem;
  border-radius: 8px;
  color: #fff;
  cursor: pointer;
}
.store span:hover {
  background-color: #246453;
}

.store span i {
  margin-right: 12px;
}

.download p {
  text-align: center;
  width: 350px;
  line-height: 1.3;
  padding-top: 1rem;
}

.features_features__B9Ny8 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* background-color: #1f4037; */
  background-color: #b9d6cf;

  padding-bottom: 2rem;
}

.features_features__B9Ny8 span {
  font-size: 18px;
  text-transform: uppercase;
  /* color: #eee; */
  color: #1f4037;
  letter-spacing: 2px;
  font-family: sans-serif;
  margin-top: 2.4rem;
}

.features_features__B9Ny8 h1 {
  /* color: #ddd; */
  color: #1f4037;
}

/* container */
.features_fContainer__35VPA {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  /* background-color: #1f4037; */
}

.features_fContent__mbSMc {
  background-color: #fff;
  border-radius: 12px;
  width: 400px;
  padding: 1rem;
  margin: 1rem;
  height: 100%;
}
.features_fContent__mbSMc p {
  font-size: 17px;
  color: #1f4037;
  text-align: center;
}
.features_features__B9Ny8 span {
  text-transform: capitalize;
}
.features_features__B9Ny8 span a {
  color: rgb(97, 91, 91);
}

.features_fContent__mbSMc button {
  background-color: #1f4037;
  padding: 8px 16px;
  border-radius: 12px;
  border: none;
  color: #fff;
  text-transform: capitalize;
  display: block;
  margin: auto;
  width: 100px;
}

.features_fContent__mbSMc button:hover {
  cursor: pointer;
  background-color: rgb(161, 160, 160);
  color: #1f4037;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(68, 65, 62);
  padding: 0.4rem 0;
}

.footer p {
  color: #ddd;
  text-align: center;
  font-size: 16px;
  width: 420px;
  font-style: oblique;
}

.homeContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #a9fad3;
  /* background: #1f4037; */
  background-color: #b9d6cf;
  /* background: linear-gradient(to right, #1f4037, #1f4037); */
  height: 100vh;
}

.left {
  background-color: rgb(255, 255, 255);
  width: 30%;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  padding-left: 1rem;
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
}
.left h3 {
  text-transform: uppercase;
  font-size: 17px;
  font-family: "Montserrat", sans-serif;
  letter-spacing: 2px;
  color: rgb(124, 122, 122);
}
.left h1 {
  width: 400px;
  font-family: sans-serif;
  font-weight: 500;
}

.left ul {
  margin-left: -1.5rem;
}
.left ul li {
  color: rgb(63, 62, 62);
  list-style: none;
}
.left ul li::before {
  content: "▶";
  font-size: 16px;
  margin-right: 1rem;
}

.left button {
  color: #fff;
  font-size: 14px;
  border: none;
  padding: 10px 16px;
  border-radius: 12px;
  background: #1f4037;
  background: linear-gradient(to right, #80c5a5, #1f4037);
  letter-spacing: 1px;
  text-transform: uppercase;
}

.left button:hover {
  cursor: pointer;
  background: #1f4037;
  background: linear-gradient(to right, #1f4037, #99f2c8);
}

.right {
  height: 500px;
}
.right img {
  border-top-right-radius: 12px;
  border-bottom-right-radius: 12px;
}

.info_info__1xc9C {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-bottom: 1.5rem;

  /* background: #1f4037; */
  background-color: #b9d6cf;
}

.info_info__1xc9C h1 {
  /* color: #fff; */
  color: #1f4037;
  margin-top: 2rem;
}

.info_info__1xc9C p {
  line-height: 1.3;
  width: 500px;
  text-align: center;
  /* color: #afb4b3; */
  color: #1f4037;
  margin-top: -0.2rem;
}

.info_info__1xc9C button {
  color: #fff;
  font-size: 14px;
  border: none;
  padding: 10px 16px;
  border-radius: 12px;
  font-family: "Roboto", sans-serif;
  letter-spacing: 1px;
  text-transform: uppercase;
  background-color: #1f4037;
}

.info_info__1xc9C button:hover {
  cursor: pointer;
  color: #1f4037;
  background-color: #fff;
}

.nav {
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
}
.nav h3 {
  margin-left: 2rem;
  color: rgba(143, 143, 143, 0.933);
  font-family: sans-serif;
}
.nav i {
  color: rgb(124, 122, 122);
  margin-right: 2rem;
}

.nav i:hover {
  cursor: pointer;
}

.satisfaction {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  padding: 3rem 160px;
}
.satisfaction h3 {
  width: 380px;
  color: #1f4037;
  font-size: 38px;
  margin-top: 2.8rem;
}

.satisfaction p {
  width: 580px;
  font-size: 17px;
  margin-top: -1rem;
}

/* cards */

.cards {
  display: flex;
  flex-wrap: wrap;
  padding: 0 140px;
}

.card {
  width: 370px;
  height: 370px;
  margin-top: 1rem;
  padding: 1rem;
}

.card span {
  font-size: 2rem;
  color: #3b967d;
  margin: 4rem 0;
}

.card p {
  font-size: 18px;
  font-family: sans-serif;
  font-weight: 500;
  color: grey;
  letter-spacing: 1px;
  width: 280px;
  line-height: 1.6;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

